import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
    createArrayOfNumbers, setCardsCount, inputFirstName,
    inputLastName, inputEmail
} from "../../actions";


class SingUp extends Component {
    constructor(props) {
        super(props);
        this.state = {
            inputNames: ["First Name", "Last Name", 'email'],
        }
    }

    inputFirstName = (event) => {
        this.props.inputFirstName(event.target.value);
    }

    inputLastName = (event) => {
        this.props.inputLastName(event.target.value);
    }

    inputEmail = (event) => {
        this.props.inputEmail(event.target.value);
    }
    


    render() {
        return (
            <div className="SingUp">
                <div className="InputForm">
                    <span>First Name</span>
                    <input onChange={this.inputFirstName} />
                </div>
                <div className="InputForm">
                    <span>Last Name</span>
                    <input onChange={this.inputLastName}/>
                </div>
                <div className="InputForm">
                    <span>email</span>
                    <input onChange={this.inputEmail}/>
                </div>
            </div>
        );
    }
}

export default connect(
    state => ({
        firstName: state.firstName,
        lastName: state.lastName,
        email: state.email,
    }),
    {
        inputFirstName,
        inputLastName,
        inputEmail,
    }
)(SingUp);



