import React from 'react';
import Rules from './Rules';
import { Link } from 'react-router-dom';


const HowToPlayContent = () => (
    <div className="HowToPlay">
        <Link to="/"><p className="back">Back To Menu</p></Link>
        <h1>How To Play</h1>
        <Rules/>
    </div>
)

export default HowToPlayContent;



