import React, { Component } from 'react';
import '../../styles/Menu.css';
import NavigateButtons from '../presentational/NavigateButtons';


class Menu extends Component {
    
    constructor(props) {
        super(props);
    }
    
    render() {
        return (
            <NavigateButtons {...this.props}/>
        );
    }
}

export default Menu;



