import React, {Component} from 'react';
import { connect } from 'react-redux';
import '../../styles/Card.css';
import { rotateFirstCard, rotateSecondCard, updateScore, win } from "../../actions";

class GameCard extends Component{
    constructor(props){
        super(props);
    }
    
    rotateCard = (event) => {
        if (event.currentTarget.className === "game-card") {
            if (this.props.firstRotated === undefined || this.props.firstRotated.elem === undefined) {
                this.props.rotateFirstCard(event.currentTarget, this.props.number);
                event.currentTarget.className += " rotated";
            } else if (this.props.secondRotated === undefined || this.props.secondRotated.elem === undefined) {
                this.props.rotateSecondCard(event.currentTarget, this.props.number);
                event.currentTarget.className += " rotated";
                setTimeout(() => this.checkCard(), 2000);
            }
        }
    }

    checkCard = () => {
        if (this.props.firstRotated.value !== 0 && this.props.secondRotated.value !== 0) {
            if (this.props.firstRotated.value === this.props.secondRotated.value) {
                this.props.firstRotated.elem.style.visibility = "hidden";
                this.props.secondRotated.elem.style.visibility = "hidden";
                this.props.rotateFirstCard(undefined, 0);
                this.props.rotateSecondCard(undefined, 0);
                this.props.updateScore(this.props.score + 10);
                if (this.props.level === "4") {
                    if (this.props.score === 60) {
                        this.sendInfo();
                        this.props.win(true);
                        document.getElementById("win").style.visibility = "visible";
                    }
                }
                if (this.props.level === "6") {
                    if ((this.props.score === 90)) {
                        this.sendInfo();
                        this.props.win(true);
                        document.getElementById("win").style.visibility = "visible";
                    }
                }
                if (this.props.level === "8") {
                    if ((this.props.score === 120)) {
                        this.sendInfo();
                        this.props.win(true);
                        document.getElementById("win").style.visibility = "visible";
                    }
                }
            } else {
                this.props.firstRotated.elem.className = "game-card";
                this.props.secondRotated.elem.className = "game-card";
                this.props.rotateFirstCard(undefined, 0);
                this.props.rotateSecondCard(undefined, 0);
            }
        } 
    }

    sendInfo = () => {
        fetch('https://mmg-score.herokuapp.com/', {
            headers: {
                'Access-Control-Allow-Credentials': 'true',
                'Access-Control-Allow-Origin': 'cors_url',
                'Content-Type': 'application/json',
            },
            method: 'POST',
            body: JSON.stringify({
                score: this.props.score,
                email: this.props.email,
                username: this.props.firstName + "_" + this.props.lastName,
            })
        }).then(function (response) {
            return response.json();
        }).then(function (data) {
            console.log(data)
        });
    }

    render() {
        return (
            <div className="game-card" onClick={this.rotateCard} onAnimationEnd={this.checkCard}>
                <div className="front side"
                    style={{ backgroundImage: this.props.card }}>
                </div>
                <div className="back side"
                    style={{ backgroundImage: this.props.imgNumber }}>
                </div>
            </div>
        )
    }
}

export default connect(
    state => ({
        level: state.LevelValue,
        card: state.CardShirt,
        firstRotated: state.rotateFirstCard,
        secondRotated: state.rotateSecondCard,
        score: state.score,
        firstName: state.firstName,
        lastName: state.lastName,
        email: state.email,
        winCheck: state.win
    }),
    {
        rotateFirstCard,
        rotateSecondCard,
        updateScore,
        win
    }
)(GameCard);





