import React, {Component} from 'react';
import Timer from './Timer';
import GameCard from './Card';
import { connect } from 'react-redux';
import '../../styles/Game.css';
import { createArrayOfNumbers, setCardsCount, win } from "../../actions";
import { Link } from 'react-router-dom';



class Game extends Component {
    constructor(props) {
        super(props);
        this.state = {
            array: undefined,
        }
    }
    createCards = column => {
        if (this.props.level === "4") {
            return [1, 2, 3, 4].map(item => {
                return <GameCard key={item + column} 
                    imgNumber={"url('../../img/game/" + this.state.array[item + column - 1] + ".png')"}
                    number={this.state.array[item + column - 1]} />;
            });
        } else if (this.props.level === "6") {
            return [1, 2, 3, 4, 5, 6].map(item => {
                return <GameCard key={item + column}
                    imgNumber={"url('../../img/game/" + this.state.array[item + column - 1] + ".png')"}
                    number={this.state.array[item + column - 1]} />;
            });
        } else if (this.props.level === "8") {
            return [1, 2, 3, 4, 5, 6, 7, 8].map(item => {
                return <GameCard key={item + column}
                    imgNumber={"url('../../img/game/" + this.state.array[item + column - 1] + ".png')"}
                    number={this.state.array[item + column - 1]} />;
            });
        }
    };

    componentWillMount() {
        let arr = this.createArrayOfNumbers();
        this.setState({
            array: arr,
        })
    }

    createArrayOfNumbers = () => {
        let array = [];
        for (let i = 1; i <= this.props.level * 1.5; i++) {
            let number = this.getRandomNumber();
            array.push(number);
            array.push(number);
        }
        array.sort((a, b) => {
            return Math.random() - 0.5;
        });
        return array;
    };

    getRandomNumber = () => {
        return Math.floor(Math.random() * (40 - 1 + 1)) + 1;
    };

    show = () => {
        if (this.props.winCheck === false) {
            return (
                <div className="Game" onLoad={this.createArrayOfNumbers}>
                    <div id="win"> You win </div>
                    <Timer />
                    <div className="Game-Cards">
                        <div className="Cards">{this.createCards(0)}</div>
                        <div className="Cards">
                            {this.createCards(+this.props.level)}
                        </div>
                        <div className="Cards">
                            {this.createCards(+this.props.level * 2)}
                        </div>
                    </div>
                </div>
            );
        } else return (
            <h1>You win</h1>
        )
    }

    render() {
        return (<this.show />)
    }
}

export default connect(
    state => ({
        level: state.LevelValue,
        cardsCount: state.cardsCount,
        arrayOfImgNumbers: state.arrayOfImgNumbers,
        score: state.score,
        card: state.card,
        firstName: state.firstName,
        lastName: state.lastName,
        email: state.email,
        winCheck: state.win
    }),
    {
        setCardsCount,
        createArrayOfNumbers
    }
)(Game);



