import React, {Component} from 'react';
import "../../styles/Level.css";
import { connect } from 'react-redux';
import { chooseLevel, chooseCard, saveCardShirt, saveLevelValue } from "../../actions";

class Level extends Component {
    chooseLevel = (event) => {
        if (typeof(this.props.level) !== "undefined") {
            this.props.level.style.border = "none";
        }
        this.props.chooseLevel(event.target);
        this.saveLevelValue(event.target.dataset.value);
        event.target.style.border = "1px solid red";
    }

    saveLevelValue = value => {
        this.props.saveLevelValue(value);
    }

    saveCardShirt = value => {
        this.props.saveCardShirt(value);
    }

    chooseCard = (event) => {
        if (typeof(this.props.card) !== "undefined") {
            this.props.card.style.border = "none";
        }
        this.props.chooseCard(event.target);
        this.saveCardShirt(event.target.dataset.value);
        event.target.style.border = "1px solid red";
    }

    render() {
        return (
            <div className="Level">
                <h1>Level</h1>
                <div className="Cards">
                    <div className="Card" data-value="url(../../img/front/2.jpg)" 
                        style={{ backgroundImage: `url(../../img/front/2.jpg)` }}
                        onClick={this.chooseCard}>
                    </div>
                    <div className="Card" data-value="url(../../img/front/3.jpg)" 
                        style={{ backgroundImage: `url(../../img/front/3.jpg)` }}
                        onClick={this.chooseCard}>
                    </div>
                    <div className="Card" data-value="url(../../img/front/1.jpg)" 
                        style={{ backgroundImage: `url(../../img/front/1.jpg)` }}
                        onClick={this.chooseCard}>
                    </div>
                </div>
                <div className="DiffLevel">
                    <div className="Easy" data-value="4"
                        onClick={this.chooseLevel}>
                        4x3
                    </div>
                    <div className="Medium" data-value="6"
                        onClick={this.chooseLevel}>
                        6x3
                    </div>
                    <div className="Hard" data-value="8"
                        onClick={this.chooseLevel}>
                        8x3
                    </div>
                </div>
            </div>
        )
    }
}

export default connect(
    state => ({
            level: state.level,
            card: state.card,
            CardShirt: state.CardShirt,
            LevelValue: state.LevelValue,
        }),
    {
        chooseLevel,
        chooseCard,
        saveCardShirt,
        saveLevelValue,
    }
)(Level);



