import { CHOOSE_LEVEL, CHOOSE_CARD, SAVE_CARD_SHIRT, SAVE_LEVEL_VALUE,
        ROTATE_FIRST_CARD, ROTATE_SECOND_CARD, SET_CARDS_COUNT,
        CREATE_ARRAY_OF_NUMBERS, INPUT_FIRST_NAME, INPUT_LAST_NAME,
        INPUT_EMAIL, GET_ALL_SETTINGS, UPDATE_SCORE, GET_RATING_INFO, WIN } from '../constants';

export const chooseLevel = (level) => ({
    type: CHOOSE_LEVEL,
    payload: level,
});

export const chooseCard = (card) => ({
    type: CHOOSE_CARD,
    payload: card,
});

export const saveCardShirt = (event) => ({
    type: SAVE_CARD_SHIRT,
    payload: event,
});

export const saveLevelValue = (value) => ({
    type: SAVE_LEVEL_VALUE,
    payload: value,
});

export const rotateFirstCard = (elem, value) => ({
    type: ROTATE_FIRST_CARD,
    payload: {elem: elem, value: value},
});

export const rotateSecondCard = (elem, value) => ({ 
    type: ROTATE_SECOND_CARD, 
    payload: { elem: elem, value: value },
});

export const setCardsCount = (value) => ({ 
    type: SET_CARDS_COUNT, 
    payload: value, 
});

export const createArrayOfNumbers = (array) => ({
    type: "CREATE_ARRAY_OF_NUMBERS",
    payload: array,
});

export const inputFirstName = (value) => ({
    type: INPUT_FIRST_NAME,
    payload: value,
});

export const inputLastName = (value) => ({
    type: INPUT_LAST_NAME,
    payload: value,
});

export const inputEmail = (value) => ({
    type: INPUT_EMAIL,
    payload: value,
});

export const getAllSettings = (value) => ({
    type: GET_ALL_SETTINGS,
    payload: value,
});

export const updateScore = (value) => ({
    type: UPDATE_SCORE,
    payload: value,
});

export const getRatingInfo = (object) => ({
    type: UPDATE_SCORE,
    payload: object,
});

export const win = (bool) => ({
    type: UPDATE_SCORE,
    payload: bool,
});



