import { CHOOSE_LEVEL, CHOOSE_CARD, SAVE_CARD_SHIRT, SAVE_LEVEL_VALUE,
        ROTATE_FIRST_CARD, ROTATE_SECOND_CARD,
        CREATE_ARRAY_OF_NUMBERS, INPUT_FIRST_NAME, INPUT_LAST_NAME,
        INPUT_EMAIL, GET_ALL_SETTINGS, UPDATE_SCORE, GET_RATING_INFO,
        WIN } from "../constants";

const InitialState = {
    level: undefined,
    card: undefined,
    CardShirt: undefined,
    LevelValue: undefined,
    rotateFirstCard: undefined,
    rotateSecondCard: undefined,
    arrayOfImgNumbers: undefined,
    firstName: undefined,
    lastName: undefined,
    email: undefined,
    score: 0,
    top10players: [],
    win: false
}

function reducer(state = InitialState, action ) {
    switch (action.type) {
        case CHOOSE_LEVEL:
            return { ...state, level: action.payload };
        case CHOOSE_CARD:
            return { ...state, card: action.payload };
        case SAVE_CARD_SHIRT:
            return { ...state, CardShirt: action.payload };
        case SAVE_LEVEL_VALUE:
            return { ...state, LevelValue: action.payload };
        case ROTATE_FIRST_CARD:
            return { ...state, rotateFirstCard: action.payload };
        case ROTATE_SECOND_CARD:
            return { ...state, rotateSecondCard: action.payload };
        case CREATE_ARRAY_OF_NUMBERS:
            return { ...state, arrayOfImgNumbers: action.payload };
        case INPUT_FIRST_NAME:
            return { ...state, firstName: action.payload };
        case INPUT_LAST_NAME:
            return { ...state, lastName: action.payload };
        case INPUT_EMAIL:
            return { ...state, email: action.payload };
        case UPDATE_SCORE:
            return { ...state, score: action.payload };
        case GET_RATING_INFO:
            return { ...state, top10players: action.payload };
        case WIN:
            return { ...state, win: action.payload };
        default:
            return state;
    }
}

export default reducer;



