import React, { Component } from 'react';
import "../../styles/Timer.css";

class Timer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            time: 0,
            running: 0,
            mins: 0,
            secs: 0,
            hours: 0,
            tenth: 0,
        }
    }

    startPause = (event) => {
        this.increment();
        event.target.style.display = "none";
    }

    increment = () => {
        if (this.state.running == 0) {
            setTimeout(() => {
                this.setState({
                    time: this.state.time + 1,
                })
                this.setState({
                    mins: Math.floor(this.state.time / 10 / 60),
                    secs: Math.floor(this.state.time / 10 % 60),
                    hours: Math.floor(this.state.time / 10 / 60 / 60),
                    tenth: this.state.time % 10,
                });
                if (this.state.mins < 10) {
                    this.setState({
                        mins: '0' + this.state.mins
                    });
                }
                if (this.state.secs < 10) {
                    this.setState({
                        secs: '0' + this.state.secs
                    });
                }
                this.increment();
            }, 100)
        }
    }



    render() {
        return (
            <div>
                <div className="btnGO" onClick={this.startPause}>
                    <div className="Go">GO</div>
                </div>
                <div className="Timer">
                    <h1><p id="current-time"><b>{this.state.hours}:{this.state.mins}:{this.state.secs}:{this.state.tenth}0</b></p></h1>
                </div>
            </div>
        )
    }
}

export default Timer;



