import React from 'react';
import { Link } from 'react-router-dom';


class NavigateButtons extends React.Component {
    constructor(props){
        super(props);
    }

    render() {
        return <div className="Start">
                <Link to="/registration">
                    <button className="btnStartGame">START GAME</button>
                </Link>
                <Link to="/how-to">
                    <button className="btnHowToPlay">
                        How To Play
                    </button>
                </Link>
                <Link to="/top-10">
                    <button className="btnTop10Players">
                        Top 10 Players
                    </button>
                </Link>
            </div>;
    }
} 

export default NavigateButtons;



